:root {
  /*
  Typography
  */
  --line-height: 1.7;
  --heading-font-weight: 400;
  --heading-text-transform: uppercase;
  --heading-font-family: var(--ff-primary);
  --h6: clamp(20px, 1.21rem + 0.16vw, 22px);
  --h5: clamp(22px, 1.28rem + 0.4vw, 26.84px);
  --h4: clamp(24.2px, 1.35rem + 0.7vw, 32.74px);
  --h3: clamp(26.62px, 1.41rem + 1.09vw, 39.95px);
  --h2: clamp(29.28px, 1.46rem + 1.59vw, 48.74px);
  --h1: clamp(36px, 1.68rem + 2.45vw, 66px);
  --fs-xs: 0.75em;
  --fs-sm: 0.875em;
  --fs-base: 1em;
  --fs-lg: 1.125em;
  --fs-xl: 1.25em;
  /*
  Structure Variables
  */
  --header-height: clamp(48px, 1.86rem + 2.39vw, 75px);
  --text-indent: clamp(30px, 1.3rem + 2.45vw, 60px);
  --spacer-element: clamp(16px, 0.87rem + 0.57vw, 23px);
  --spacer-element-sm: calc(var(--spacer-element) / 2);
  --spacer-element-md: clamp(16px, 0.43rem + 2.45vw, 46px);
  --spacer-section: clamp(30px, 1.11rem + 3.27vw, 70px);
  --spacer-base: clamp(20px, 1.06rem + 0.82vw, 30px);
  --spacer-1: calc(var(--spacer-base) * 0.33);
  --spacer-2: calc(var(--spacer-base) * 0.66);
  --spacer-3: calc(var(--spacer-base));
  --spacer-4: calc(var(--spacer-base) * 1.33);
  --spacer-5: calc(var(--spacer-base) * 1.66);
  --container-width: 1440px;
  --container-padding: clamp(16px, 0.58rem + 1.8vw, 38px);
  --decor-width: 70%;
  --input-height: 3em;
  --input-x-padding: 1.25em;
  --input-y-padding: 0.75em;
  --border-width: 1px;
  --shadow: 0 0.25em 0.5em 0.5em var(--color-shadow);
  /*
  Transitions
  */
  --transition-duration-value: 300;
  --transition-duration: calc(1ms * var(--transition-duration-value));
  --transition-timing-function: ease;
  --transition: var(--transition-duration) var(--transition-timing-function);
  --animate-transition: 500ms ease;
  --animate-distance: 1.5rem;
}
@media (min-width: 768px) {
  :root {
    --decor-width: 50%;
  }
}

