  margin-left: auto;
  margin-right: auto;
}

#wpadminbar {
  position: relative;
  z-index: 1600;
}

@media (max-width: 991px) {
  .is-locked {
    overflow: hidden;
  }
}
.align-items-center {
  align-items: center;
}
.align-items-end {
  align-items: flex-end;
}

.justify-content-center {
  justify-content: center;
}
.justify-content-between {
  justify-content: space-between;
}
.justify-content-end {
  justify-content: flex-end;
}

.flex-column-reverse {
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .flex-md-row {
    flex-direction: row;
  }
}

.stretch-child > :first-child,
.stretch {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}

.pos-rel {
  position: relative;
}
.pos-fixed {
  position: fixed;
}

.overflow-hidden {
  overflow: hidden;
}

.element-collapsing {
  height: 0;
  overflow: hidden;
  transition: height var(--transition);
}

.element-collapse:not(.element-show) {
  display: none;
}

.screen-reader-text {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

.icon-label {
  display: inline-flex;
  gap: var(--spacer-element-sm);
  align-items: center;
  text-decoration-color: transparent;
}
.icon-label:hover {
  text-decoration-color: currentColor;
}

.vstack {
  display: flex;
  flex-direction: column;
  gap: var(--spacer-element);
}
.vstack > * {
  margin-top: 0;
  margin-bottom: 0;
}

.hstack {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: var(--spacer-element);
}
.hstack > * {
  margin-top: 0;
  margin-bottom: 0;
}

.text-indent {
  padding-left: var(--text-indent);
}

.site-subtitle {
  color: var(--color-primary);
  text-decoration: underline;
  text-underline-offset: 20%;
  text-decoration-thickness: var(--border-width);
  text-decoration-color: currentColor;
}

.custom-logo {
  max-height: var(--header-height);
  width: 100%;
  padding: 0.3em 0;
}

.site-decor {
  position: absolute;
  left: 50%;
  top: 50%;
  width: var(--decor-width);
  background: var(--color-primary);
  opacity: 0.8;
  border-radius: 50%;
  aspect-ratio: 1;
  translate: -50% -50%;
  transition: width calc(var(--transition-duration) * 1.4) var(--transition-timing-function);
}
a:hover .site-decor {
  width: 150%;
}

.banner-decor {
  padding: 20% 10% 10%;
  background: var(--color-surface-solid);
  border-radius: 100rem 100rem 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 2/1;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .banner-decor {
    padding: 10% 10% 5%;
    max-width: 70%;
  }
}

.uppercase {
  text-transform: uppercase;
}

.search-form {
  display: flex;
  flex-wrap: wrap;
}
.search-form label {
  flex: 1 0 auto;
}
.simpleParallax {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}

.img-parallax {
  transform: scale(1.5);
}

[data-animate] {
  opacity: 0;
  transition: opacity var(--animate-transition), translate var(--animate-transition);
}
[data-animate].animate-show {
  translate: 0 0;
  opacity: 1;
}

[data-animate=up] {
  translate: 0 var(--animate-distance);
}

[data-animate=down] {
  translate: 0 calc(var(--animate-distance) * -1);
}

[data-animate=left] {
  translate: var(--animate-distance) 0;
}

[data-animate=right] {
  translate: calc(var(--animate-distance) * -1) 0;
}

[data-animate-delay="100"] {
  transition-delay: 100ms;
}

[data-animate-delay="200"] {
  transition-delay: 200ms;
}

[data-animate-delay="300"] {
  transition-delay: 300ms;
}

[data-animate-delay="400"] {
  transition-delay: 400ms;
}

[data-animate-delay="500"] {
  transition-delay: 500ms;
}

.m-0 {
  margin: 0;
}
.mb-0 {
  margin-bottom: 0;
}

/*
  Add manually if need:
  .spacer-section-p*-sm
  .spacer-section-p*-lg
  .spacer-section-m*-lg
  .spacer-section-m*-sm
  etc.
*/
.spacer-section-pt, .spacer-section-py {
