  color: var(--color-white);
}
.color-text-white a:not([class]) {
  color: inherit;
}
.color-text-muted {
  color: var(--color-text-muted);
}

.min-height-small {
  min-height: clamp(300px, 10.1rem + 18.03vw, 450px);
}
.min-height-medium {
  min-height: clamp(400px, 13.46rem + 24.04vw, 600px);
}
.min-height-large {
  min-height: clamp(500px, 13.94rem + 36.06vw, 800px);
}
.min-height-fullheight {
