}
.rotate-180 {
  rotate: 180deg;
}
.rotate-270 {
  rotate: 270deg;
}

.button, .search-submit {
  padding: 0 calc(var(--input-x-padding) * 1.4);
  min-height: var(--input-height);
  display: inline-flex;
  justify-content: center;
  gap: var(--spacer-element-sm);
  align-items: center;
  background: var(--color-button-background);
  border: var(--border-width) solid var(--color-button-border);
  color: var(--color-button-text);
  text-decoration: none;
  cursor: pointer;
  text-transform: uppercase;
  border-radius: 0;
  text-align: center;
  transition: border-color var(--transition), color var(--transition), background var(--transition), filter var(--transition);
}
.button:hover, .search-submit:hover {
  background: var(--color-button-background-hover);
  border-color: var(--color-button-background-hover);
  color: var(--color-white);
}

.button--outline {
  background: none;
  color: inherit;
  border-color: currentColor;
}
.button--white {
  background: var(--color-white);
  color: var(--color-primary);
  border-color: transparent;
}
.button--surface {
  background: var(--color-surface);
  border-color: transparent;
  color: inherit;
}
.button--square {
  padding: 0.5em;
  min-width: var(--input-height);
}
.button--backgroundless:not(:hover):not(:focus) {
  background-color: transparent;
  color: inherit;
  border-color: transparent;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: var(--spacer-element-sm);
}
.accordion__content {
  overflow: hidden;
  padding: 0 var(--input-x-padding);
}
.accordion__item {
  background-color: var(--color-surface);
}
.site-footer .accordion__item {
  background-color: transparent;
}
.accordion__button {
  width: 100%;
  text-transform: capitalize;
}
.accordion__button:not([aria-expanded=true]) {
  color: inherit;
  border-color: transparent;
  background-color: var(--color-surface);
}
.site-footer .accordion__button:not([aria-expanded=true]) {
  color: currentColor;
  border-color: transparent;
  background-color: var(--color-white);
}
.accordion__icon {
  font-size: 1.5em;
  flex: 0 0 auto;
  transition: transform var(--transition);
}
.accordion__button[aria-expanded=true] .accordion__icon {
  transform: rotate(180deg);
}
.accordion__title {
  font-size: 1em;
  margin: 0;
}
.accordion__label {
  text-align: left;
  flex-grow: 1;
}

.card {
  color: inherit;
  height: 100%;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  text-align: center;
  gap: var(--spacer-element-sm);
}
.card__body {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
  gap: var(--spacer-element-sm);
}
.card__content {
  padding: var(--spacer-element-sm) 0;
}
.card__content > * {
  margin: 0;
}
.card__header {
  position: relative;
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--spacer-element);
}
.card__title {
  position: relative;
  color: var(--color-white);
}
.card__content + .card__link {
  background: var(--color-surface);
}
.card__link {
  padding: var(--spacer-element-sm);
  color: currentColor;
  margin-top: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  gap: var(--spacer-element-sm);
}
a.card:hover {
  color: inherit;
}
a.card:hover .card__icon {
  opacity: 1;
}

.container, .container-fluid, .container-xl, .container-lg, .container-md, .container-sm, .container-xs {
  padding-left: var(--container-padding);
  padding-right: var(--container-padding);
  margin-left: auto;
  margin-right: auto;
  max-width: var(--container-width);
  width: 100%;
}
.container-xs {
  max-width: calc(var(--container-width) * 0.4);
}
.container-sm {
  max-width: calc(var(--container-width) * 0.6);
}
.container-md {
  max-width: calc(var(--container-width) * 0.8);
}
.container-lg {
  max-width: calc(var(--container-width) * 1.2);
}
.container-xl {
  max-width: calc(var(--container-width) * 1.4);
}
.container-fluid {
  max-width: none;
}
@media (min-width: 768px) {
  .container--panel {
    padding-left: var(--spacer-section);
    padding-right: var(--spacer-section);
  }
}

.curtain {
  background: var(--color-curtain);
}
.curtain--hidden {
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition), visibility var(--transition);
}
.curtain--visible {
  opacity: 1;
  visibility: visible;
}

.gallery {
  display: grid;
  gap: var(--spacer-element);
  grid-template-columns: 3fr 2fr 3fr 2fr;
  grid-auto-rows: minmax(0, 23vw);
  grid-auto-flow: dense;
}
.gallery > a {
  grid-column: span 2;
}
@media (max-width: 767px) {
  .gallery > a:nth-child(2n+1) {
    grid-row: span 2;
  }
}
@media (min-width: 768px) {
  .gallery > a:nth-child(5n+2) {
    grid-row: span 2;
  }
  .gallery > a:nth-child(5n+3), .gallery > a:nth-child(6n+4) {
    grid-column: span 1;
  }
  .gallery > a:last-child {
    grid-column: span 1;
    grid-row: span 1;
  }
}
.galleries .gallery {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition);
}
.galleries .gallery:not(.active) {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: -100;
  top: 0;
  left: 0;
}
.gallery__images {
  position: relative;
  padding: 0 var(--spacer-element);
}
.gallery img {
  height: 100%;
  width: 100%;
}

.tab {
  width: 100%;
  justify-content: flex-start;
}
.tabs__menu {
  margin: 0;
}
.tabs__menu .tab__icon {
  transform: rotate(-90deg);
}
.tab:not(.tab--active):not(:hover) {
  background-color: var(--color-surface);
  border-color: transparent;
  color: currentColor;
}
.tab--active {
  cursor: default;
}
.tab__title {
  font-size: 1em;
  margin: 0;
  text-align: left;
  text-transform: none;
}
.tab:not(:first-of-type) {
  margin-top: var(--spacer-element-sm);
}
.tab__icon {
  flex: 0 0 auto;
  font-size: 1.5em;
  transition: transform var(--transition);
}
.tab__content {
  overflow: hidden;
  width: 100% !important;
}
.tab--accordion.tab--active .tab__icon {
  transform: rotate(-180deg);
}

ul.page-numbers,
.pagination {
  list-style: none;
  padding: 0;
  margin: var(--spacer-element-md) 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.5rem;
}
ul.page-numbers li,
.pagination li {
  margin: 0;
}
ul.page-numbers .page-numbers,
.pagination .page-numbers {
  padding: 0 calc(var(--input-x-padding) * 1.4);
  min-height: var(--input-height);
  display: inline-flex;
  justify-content: center;
  gap: var(--spacer-element-sm);
  align-items: center;
  background: var(--color-button-background);
  border: var(--border-width) solid var(--color-button-border);
  color: var(--color-button-text);
  text-decoration: none;
  cursor: pointer;
  text-transform: uppercase;
  border-radius: 0;
  text-align: center;
  transition: border-color var(--transition), color var(--transition), background var(--transition), filter var(--transition);
  padding: 0.5em;
  min-width: var(--input-height);
}
ul.page-numbers .page-numbers .site-icon,
.pagination .page-numbers .site-icon {
  font-size: 1.4em;
}
ul.page-numbers .page-numbers:not(.current):not(:hover),
.pagination .page-numbers:not(.current):not(:hover) {
  background: var(--color-surface);
  border-color: transparent;
  color: currentColor;
}
ul.page-numbers .page-numbers.current,
.pagination .page-numbers.current {
  pointer-events: none;
}

.site-icon {
  width: 1em;
  height: 1em;
  display: inline-flex;
}
.site-icon--chevron {
  background-color: currentColor;
  mask-image: url("../sprite/sprite.svg#chevron-usage");
}
.site-icon--close {
  background-color: currentColor;
  mask-image: url("../sprite/sprite.svg#close-usage");
}
.site-icon--left {
  rotate: 90deg;
}
.site-icon--right {
  rotate: -90deg;
}
.site-icon--arrow {
  flex: 0 0 auto;
  rotate: -45deg;
  transition: rotate var(--transition);
  vertical-align: middle;
}
a:hover .site-icon--arrow {
  rotate: 0deg;
}
.site-icon-rounded {
  background: var(--color-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5em;
  height: 2.5em;
  flex: 0 0 auto;
  border-radius: 100%;
  transition: background-color var(--transition), color var(--transition);
  color: var(--color-white);
}
.site-icon-rounded:hover, a:hover .site-icon-rounded {
  background: var(--color-button-background-hover);
}

.skip-link {
  position: fixed;
  left: var(--container-padding);
  top: calc(var(--header-height) + var(--container-padding));
  background-color: var(--color-white);
  font-weight: bold;
  padding: 1rem;
  z-index: 1000;
}
.skip-link:not(:focus) {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

.social-icons {
  list-style: none;
  padding: 0;
  display: inline-flex;
  align-items: center;
  flex-wrap: wrap;
  gap: var(--spacer-element-sm);
  margin: 0;
